import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
import Toast from '../../wxcomponents/vant/dist/toast/toast';
var  app = getApp().globalData
export default {
	data() {
		return {
			nickname:'',
			showTitle:false,
			company_id:'',
			showActive:false,
			show:false,
			loading:false,
			allowloading:false,
			title: 'Hello',
			is_pay: false, // 提示支付弹窗
			banner: [], // 轮播
			animationData: {}, //自定义动画
			page:1,
			hotList:[], //热门项目
			tile:[], // 咖啡厅等
			icon:[], // 会员服务等小图标
			banner2:{},
			iconShow:1  //1是续费，2是开通
		}
	},
	onPullDownRefresh(){
		this.page = 1
		this.getHot()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	onReachBottom(){
		if(this.allowloading){
			this.allowloading = false
			this.loading = true
			this.page++
			this.getHot()
		}
	},
	onHide() {
		this.showActive = false
	},
	onLoad(option) {
		// 获取图片和颜色
		this.getImg()
		// 获取轮播图数据
		this.getData()
		this.getData1()
		// 获取热门项目
		this.getHot()
		var that = this
		var date = new Date() / 1000
		if (uni.getStorageSync("expiretime")) { // 登录过
				this.getLocationInfo();
		        // if (uni.getStorageSync("setInfo")== 1) {
					// this.getLocationInfo();
		          if (uni.getStorageSync('isVip') == 0) { // 未支付
				      // console.log('未支付')
		            if (uni.getStorageSync('tips_pay')) { //检测
		              uni.showTabBar()
					  that.is_pay = false
					  this.getLocationInfo();
		            } else {
		              that.is_pay = true
					  uni.hideTabBar()
		            }
		          } else if (uni.getStorageSync('isVip') == 1) {
		            // console.log('IsVip')
		          }
		        // } else if (uni.getStorageSync("setInfo") == 0) {
		        //   uni.reLaunch({
		        //   	 url:'../PersonalIdcard/PersonalIdcard'
		        //   }); // 完善信息
		        // }
		} else if (uni.getStorageSync("expiretime")==='' || uni.getStorageSync("expiretime")===undefined ||uni.getStorageSync("expiretime")===null || date > uni.getStorageSync("expiretime")) { // 未登录过或登录过已过期
			uni.clearStorageSync()
			uni.showToast({
			    title: '请登录',
			    duration: 2000,
				icon:'none'
			});
			this.$nextTick(function(){
				uni.reLaunch({
					url: '/pages/login/login'
				})
			})
			 
		}
	},
	onShow(){
		this.$api.MyselfIndex({
			token: uni.getStorageSync("token"),
			user_id: uni.getStorageSync("userId")
		}).then(res1=>{
			// console.log(res1.data.data.nickname)
			this.nickname = res1.data.data.nickname
			if(res1.data.data.is_vip==1){
				this.iconShow = 1
			}else{
				this.iconShow = 2
			}
			this.$api.is_get_badge({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				if(res.data.code=='1079'){
					uni.setStorageSync("isVip",1)
					// 弹窗跳填写地址
					Dialog.confirm({
					  title: '提示',
					  message: '尊敬的'+res.data.data.nickname+'先生/女士：您已缴费成功，德申汇为您献上一份专属礼包，填写地址，马上领取',
					  confirmButtonText:'立即领取'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/PayStep/PayStep'
						})
					}).catch(() => {
					  // on cancel
					});
				}else if(res.data.code=='1081'){
					uni.setStorageSync("isVip",1)
				}
			})
		})
		// 国外实名认证是否通过
		this.getforeign()
		
		// 动态创建遮罩特效
		var animation = uni.createAnimation({
		      duration: 300,
		      timingFunction: 'linear',
		})			
		this.animation = animation			
		animation.translateY(-500).step()			
		this.animationData = animation.export()
		setTimeout(function() {
		  animation.translateY(0).step()
		  this.animationData = animation.export()
		}.bind(this), 300)
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		    return {
		      title:this.nickname +'邀请您加入德申汇',
		      path: "/pages/login/login?userId="+uni.getStorageSync('userId'),
		      imageUrl:app.shareImg
		    }
		}
	    return {
	      title: '德申汇小程序',
	      path: "/pages/index/index",
		  imageUrl:app.shareImg
	    }
	},
	methods: {
		// 胶囊跳转
		capsule(item){
			uni.navigateTo({
				url:'../../pagesMy/view/view?url='+ item.href
			})
		},
		coffee(){
			uni.navigateTo({
				url:'../../pagesMy/view/view?url=https://web.detion.com/CoffeeIndex'
			})
		},
		// 获取图片
		getImg(){
			this.$api.home_page_img_2({
				version:3
			}).then(res=>{
				this.tile = res.data.data.imgs.tile
				this.icon = res.data.data.imgs.icon
			})
		},
		// 了解德申
		detion(){
			uni.navigateTo({
				url:'../../pagesMy/view/view?url=https://web.detion.com/Detion'
			})
			// uni.redirectTo({
			// 	url:'/pagesFour/ServiceStatus/ServiceStatus?type=0'
			// })
			// uni.navigateTo({
			// 	url:'/pagesMy/Upload/Upload'
			// })
		},
		// 邀请好友
		InviteFriends(){
			uni.navigateTo({
				url:'../../pagesThree/InviteFriends/InviteFriends'
			})
		},
		// 会员服务
		vip(){
			if (Number(uni.getStorageSync('setInfo')) === 1) {
				this.$api.is_get_badge({
				  token: uni.getStorageSync("token"),
				  user_id: uni.getStorageSync("userId")
				}).then(res => {
				  if (res.data.code === '1016') { // 不是vip
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
				  } else if (res.data.code === '1079') { // 缴费成功未选择礼包
						uni.navigateTo({
							url:'../../pagesMy/PayStep/PayStep'
						})
				  } else if (res.data.code === '1081' || res.data.code === '1077') { // 10000之后领取礼包完成可以正常续费或10000之前没有领取资格
						uni.navigateTo({
							url:'../../pagesMy/Renew/Renew'
						})
				  } else if (res.data.code === '1099') {
						uni.navigateTo({
							url:'../../pages/PersonalIdcard/PersonalIdcard'
						})
				  } else {
					uni.showToast({
					    title: res.data.msg,
					    mask: "true",
						icon:'none'
					});
				  }
				})
			  } else if (Number(uni.getStorageSync('setInfo')) === 0) {
				uni.navigateTo({
					url:'../../pages/PersonalIdcard/PersonalIdcard'
				})
			  }
		},
		swiper(item){
			// console.log(item)
			uni.navigateTo({
				url:"../../pagesMy/view/view?url="+item.href
			})
		},
		search(){
			uni.navigateTo({
				url:'../../pagesMy/Search/Search'
			})
		},	
		LuDingJi(){
			uni.navigateTo({
				url:'../../pagesMy/LuDingJi/LuDingJi'
			})
		},
		SmallStretchNew(){
			uni.navigateTo({
				url:'../../pagesTwo/SmallStretchNew/SmallStretchNew'
			})
		},
		LocalLife(){
			uni.navigateTo({
				url:'../../pagesMy/LocalLife/LocalLife'
			})
		},
		// 商城
		goShop(){
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 1000);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,去支付？',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					  this.$api.shop_mobile({
					  	token: uni.getStorageSync("token"),
					  	user_id: uni.getStorageSync("userId"),
					  	
					  }).then(res=>{
						  // console.log(res)
						  uni.navigateTo({
							  url:'../../pagesMy/view/view?url=http://shop.detion.com'+'&accounts=' + res.data.data.accounts +'&pwd=' + res.data.data.pwd
						  })
					  })
					
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
		},
		// 活动界面
		goActivity(){
			uni.navigateTo({
				url:'../../pagesTwo/Activity/Activity'
			})
		},
		goMy(){
			// console.log(123)
			if(uni.getStorageSync('setInfo') ==0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 1000);
				
			}else{
				// console.log(456)
				if (uni.getStorageSync('isVip') == 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,去支付？',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') == 1) {
					  // uni.navigateTo({
					  // 	url:'../../pagesMy/ceshi/ceshi'
					  // })
						uni.navigateTo({
							url:'../../pagesMy/MyProject/MyProject'
						})
				  } 
			}
		},
			
		goClub(){
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 1000);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,去支付？',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					uni.navigateTo({
						url:'../../pagesTwo/Club/Club'
					})
				  } 
			}
			
		},
		goEnterprise(){
			if(uni.getStorageSync('setInfo') === 0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 1000,
					icon:'none'
				});
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 1000);
			}else{
				this.$api.myself_company({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId")
				}).then(res=>{
					// console.log(res)
					if(res.data.code === '1054'){
						uni.navigateTo({
							url:'../../pagesTwo/EnterpriseCertification/EnterpriseCertification'
						})
					}else if(res.data.code === '200'){
						this.company_id = res.data.data.id
						uni.setStorageSync('company_id',Number(this.company_id))
						uni.navigateTo({
							url:'/pagesTwo/MyBusiness/MyBusiness?id='+uni.getStorageSync("company_id")
						})
					}
				})
				
				
			}
		},
		goTradingVolume(){
			uni.navigateTo({
				url:'../../pagesTwo/TradingVolume/TradingVolume'
			})
		},
		getforeign(){
			this.$api.register_applies_status({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				if(res.data.code === '1022'){
					uni.setStorageSync("setInfo",1)
				}
				
			})
		},
		
		// 会友汇详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		// 跳转筛选
		goProjectList(){
			uni.navigateTo({
				url:'../../pagesTwo/ProjectList/ProjectList'
			})
		},
			
		// 跳转个人主页
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 返回顶部
		top(){
			uni.pageScrollTo({
			    scrollTop: 0,
			    duration: 300
			});
		},
		// 地理位置

		getLocationInfo(){  //1. 获取地理位置
			var _this=this;
			uni.getLocation({
				type: 'wgs84',
				success (res) {
					let latitude,longitude;
					latitude = res.latitude.toString();
					longitude = res.longitude.toString();
					
					uni.request({
						header:{
							"Content-Type": "application/text"
						},
						// url:'https://api.map.baidu.com/geocoder/v2/?ak=YA8xdgvGfK7k8cES9kOau8GaEhXjkr9y&location=' + latitude + ',' + longitude + '&output=json',
						url:'https://apis.map.qq.com/ws/geocoder/v1/?location='+latitude+','+longitude+'&key=OJHBZ-W72Y4-W3MUW-XMF3J-MAWUJ-DLFKN',
						success(re) {
							// console.log(re)
							var wxcity = re.data.result.address_component.city
							app.city = wxcity.substring(0, wxcity.length - 1)
							uni.setStorageSync("city",wxcity.substring(0, wxcity.length - 1))
							uni.showToast({
								title:'当前城市：'+ wxcity.substring(0, wxcity.length - 1) ,
								icon:"none"	,
							})
							if(re.statusCode === 200){
								// console.log("获取中文街道地理位置成功")
							}else{
								console.log("获取信息失败，请重试！")
							}
						 }
					});
				}
			});
		},

		// 关闭遮罩和去支付
		close () {
		  uni.showTabBar()
		  this.is_pay = false
		  uni.setStorageSync("tips_pay",true)
		},
		pay () {
		  uni.showTabBar()
		  this.is_pay = false
		  uni.setStorageSync("tips_pay",true)
		  uni.navigateTo({
		  	url:'../../pagesMy/pay/pay'
		  })
		},
		
		// 获取热门
		getHot () {
		      this.$api.Opportunity_index({
		              token: uni.getStorageSync("token"),
		              user_id: uni.getStorageSync("userId"),
		              page: this.page
		            }).then(res => {
						if(res.data.code=== '200'){
							if(this.page===1){
								this.hotList = res.data.data
								if(res.data.data.length>=20){
									this.allowloading= true
									this.loading =false
								}else{
									this.allowloading= false
									this.loading =false
									this.showTitle = true
								}
							}else{
								this.hotList = this.hotList.concat(res.data.data)
								if(res.data.data.length>=20){
									this.allowloading= true
									this.loading =false
								}else{
									this.allowloading= false
									this.loading =false
									this.showTitle = true
								}
							}
							
						}
		            })
	
		},
		// 获取轮播图和热搜数据
		getData () {
		      // 热搜、轮播
		      this.$api.Swiper({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId")
		      }).then(res => {
			
		        if (res.data.code === '200') {
		          this.banner = res.data.data.resource_banner
		          // this.isFinish = true
		        } else if (res.data.code === '1010') {
				  uni.clearStorageSync()
				  uni.showToast({
				  	title:'登录过期，请重新登录',
					icon:"none"
				  })
				  uni.reLaunch({
				  	url:"../login/login"
				  })
		        }
		  
		      })
		},
		getData1 () {
		      // 热搜、轮播
		      this.$api.Swiper({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId"),
				type:2
		      }).then(res => {
				 this.banner2 = res.data.data.resource_banner
		    })
		}
			

	}
}
